<template>
  <div class="uploadfile">
    <div>
      <div class="upload" style="float:left;">
        <el-upload
          action
          :on-change="uploadFile"
          :auto-upload="false"
          :show-file-list="false"
          accept=".xls, .xlsx"
          :on-remove="handleRemove"
        >
          <el-button size="small" type="primary"></el-button>
        </el-upload>
      </div>
      <div class="operation">
        <el-button size="small" type="primary" style="margin-left:30px;" @click="upload"></el-button>
      </div>
    </div>
    <div slot="tip" class="el-upload__tip">    xlsx / xls  </div>

    <hr />
    <div>
      <div class="signdetail">
        <el-table :data="SignDetails" height="600" border style="width: 100%">
          <el-table-column prop="studentName" label="" width="130"></el-table-column>
          <el-table-column prop="grade" label="" width="130"></el-table-column>
          <el-table-column prop="number" label="" width="150"></el-table-column>
          <el-table-column prop="course" label="" width="130"></el-table-column>
          <el-table-column prop="checkType" label="" width="150"></el-table-column>
          <el-table-column prop="checkTime" label="" width="150"></el-table-column>
          <el-table-column prop="signoutTime" label="" width="150"></el-table-column>
          <el-table-column prop="sign_state" label="" width="120"></el-table-column>
          <el-table-column prop="teacher" label="" ></el-table-column>
        </el-table>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "UploadFile",
  data() {
    return {
      fileTemp: null,
      SignDetails: []
    };
  },
  methods: {
    handleRemove(file, fileList) {
      this.fileTemp = null;
    },
    upload() {
      console.log(".....");
      this.$http.post("/upload",this.SignDetails).then((res)=>{
          if (res.data.code == 200) {
            this.$notify({
              type:'success',
              message:''
            });
          }else {
            this.$message({
              type:'error',
              message:''
            });
          }
      }).catch((err)=>{
          this.$message({
              type:'error',
              message:err.data.message
            });
      })
    },
    //excel
    uploadFile(file, fileList) {
      this.fileTemp = file.raw;
      console.log(this.fileTemp);
      if (this.fileTemp) {
        if (
          this.fileTemp.type ==
            "application/vnd.openxmlformats-officedocument.spreadsheetml.sheet" ||
          this.fileTemp.type == "application/vnd.ms-excel"
        ) {
          //if(!/\.(xls|xlsx)$/.test(this.fileTemp.name.toLowerCase()))
          this.importFile(this.fileTemp, outdata => {
            //
            let arr = [];
            outdata.map(v => {
              let obj = {};
              obj.studentName = v[""];
              obj.number = v[""];
              obj.grade = v[""]
              obj.course = v[""];
              obj.checkType = v[""];
              obj.checkTime = v[""];
              obj.signoutTime = v[""];
              obj.sign_state = v[""];
              obj.teacher = v[""]
              arr.push(obj);
            });
            this.SignDetails = arr;
          });
        } else {
          this.$message({
            type: "warning",
            message: ""
          });
        }
      } else {
        this.$message({
          type: "warning",
          message: ""
        });
      }
    },
    //
    importFile(obj, callback) {
      let _this = this;
      // DOM
      this.file = obj;
      var rABS = false; //
      var f = this.file;
      var reader = new FileReader();
      FileReader.prototype.readAsBinaryString = function(f) {
        var binary = "";
        var rABS = false; //
        var pt = this;
        var wb; //
        var outdata;
        var reader = new FileReader();
        reader.onload = function(e) {
          var bytes = new Uint8Array(reader.result);
          var length = bytes.byteLength;
          for (var i = 0; i < length; i++) {
            binary += String.fromCharCode(bytes[i]);
          }
          var XLSX = require("xlsx");
          if (rABS) {
            wb = XLSX.read(btoa(fixdata(binary)), {
              //
              type: "base64"
            });
          } else {
            wb = XLSX.read(binary, {
              type: "binary"
            });
          }
          outdata = XLSX.utils.sheet_to_json(wb.Sheets[wb.SheetNames[0]]); //outdata
          callback(outdata);
          _this.$message({
            type: "success",
            message: ""
          });
        };
        reader.readAsArrayBuffer(f);
      };

      if (rABS) {
        reader.readAsArrayBuffer(f);
      } else {
        reader.readAsBinaryString(f);
      }
    }
  }
};
</script>


