<template>
  <div class="uploadfile">
    <div>
      <div class="upload" style="float:left;">
        <el-upload
          action
          :on-change="uploadFile"
          :auto-upload="false"
          :show-file-list="false"
          accept=".xls, .xlsx"
          :on-remove="handleRemove"
        >
          <el-button size="small" type="primary"></el-button>
        </el-upload>
      </div>
      <div class="operation">
        <el-button size="small" type="primary" style="margin-left:30px; float:left;" @click="upload"></el-button>
      </div>
      <div class="operation">
        <el-button size="small" type="primary" style="margin-left:30px;" @click="download"></el-button>
      </div>
    </div>
    <div slot="tip" class="el-upload__tip">    xlsx / xls  </div>

    <hr />
    <div>
      <div class="students">
        <el-table :data="StudentInfo" height="640" border style="width: 100%">
          <el-table-column prop="classNo" label="" width="310"></el-table-column>
          <el-table-column prop="className" label="" width="310"></el-table-column>
          <el-table-column prop="studentId" label="" width="310"></el-table-column>
          <el-table-column prop="studentName" label="" width="310"></el-table-column>
        </el-table>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "UploadStudentInfo",
  data() {
    return {
      fileTemp: null,
      StudentInfo: [],
      excelData:[{"studentId":'2104170010',"studentName":"","className":"(1)","teacherName":""}]
    };
  },
  methods: {
    download(){
      require.ensure([], () => {
        const { export_json_to_excel } = require("@/excel/Export2Excel.js");
        const tHeader = ["", "", "", ""]; //tHeaderExcel
        const filterVal = [
          "studentId",
          "studentName",
          "className",
          "teacherName"
        ];
        const list = this.excelData; // this.SignDetails
        const data = this.formatJson(filterVal, list); //filterValres
        export_json_to_excel(tHeader, data, ""); //''Excel
      });
    },
    formatJson(filterVal, jsonData) {
      return jsonData.map(v => filterVal.map(j => v[j]));
    },
    handleRemove(file, fileList) {
      this.fileTemp = null;
    },
    upload() {
      console.log(".....");
      this.$http.post("/uploadStudents",this.StudentInfo).then((res)=>{
          if (res.data.code == 200) {
            this.$notify({
              type:'success',
              message:''
            });
            this.$router("/StudentManager");
          }else if(res.data.code == 1001){
              this.$notify({
              type:'error',
              message:''
            });
          }else {
            this.$message({
              type:'error',
              message:''
            });
          }
      }).catch((err)=>{
          this.$message({
              type:'error',
              message:err.data.message
            });
      })
    },
    //excel
    uploadFile(file, fileList) {
      this.fileTemp = file.raw;
      console.log(this.fileTemp);
      if (this.fileTemp) {
        if (
          this.fileTemp.type ==
            "application/vnd.openxmlformats-officedocument.spreadsheetml.sheet" ||
          this.fileTemp.type == "application/vnd.ms-excel"
        ) {
          //if(!/\.(xls|xlsx)$/.test(this.fileTemp.name.toLowerCase()))
          this.importFile(this.fileTemp, outdata => {
            //
            let arr = [];
            outdata.map(v => {
              let obj = {};
              obj.classNo = v[""];
              obj.className = v[""]
              obj.studentId = v[""];
              obj.studentName = v[""];
              arr.push(obj);
            });
            this.StudentInfo = arr;
          });
        } else {
          this.$message({
            type: "warning",
            message: ""
          });
        }
      } else {
        this.$message({
          type: "warning",
          message: ""
        });
      }
    },
    //
    importFile(obj, callback) {
      let _this = this;
      // DOM
      this.file = obj;
      var rABS = false; //
      var f = this.file;
      var reader = new FileReader();
      FileReader.prototype.readAsBinaryString = function(f) {
        var binary = "";
        var rABS = false; //
        var pt = this;
        var wb; //
        var outdata;
        var reader = new FileReader();
        reader.onload = function(e) {
          var bytes = new Uint8Array(reader.result);
          var length = bytes.byteLength;
          for (var i = 0; i < length; i++) {
            binary += String.fromCharCode(bytes[i]);
          }
          var XLSX = require("xlsx");
          if (rABS) {
            wb = XLSX.read(btoa(fixdata(binary)), {
              //
              type: "base64"
            });
          } else {
            wb = XLSX.read(binary, {
              type: "binary"
            });
          }
          outdata = XLSX.utils.sheet_to_json(wb.Sheets[wb.SheetNames[0]]); //outdata
          callback(outdata);
          _this.$message({
            type: "success",
            message: ""
          });
        };
        reader.readAsArrayBuffer(f);
      };

      if (rABS) {
        reader.readAsArrayBuffer(f);
      } else {
        reader.readAsBinaryString(f);
      }
    }
  }
};
</script>


